:root {
    --editor-bg: #eee;
    --editor-border: #3f87a6;
}

/* Reset Styles */
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
    width: 100%;
}

.editor {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.editor_wrapper {
    width: 50%;
    height: 70%;
    display: flex;
    flex-direction: column;
    background: var(--editor-bg);
    padding: 2em;
    border-radius: 3px;
    border: 1px solid #ccc;
}

.editor_body {
    flex: 1;
}

.editor_footer {
    display: flex;
}

.editor_footer--left {
    display: flex;
    flex-direction: column;
}

.editor_footer--right {
    flex: 1;
    margin-left: 10px;
    overflow: hidden;
}

.editor_btn {
    border: 0;
    padding: .5em;
    background-color: #fff;
    border: 2px solid #333;
    margin-top: 1em;
    width: 100px;
    cursor: pointer;
}

.editor_console {
    background-color: #fff;
    width: 50%;
    height: 130px;
    margin-top: .9em;
    border: 1px solid var(--editor-border);
    overflow: auto;
}

.editor_code {
    position: relative;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    border: 1px solid var(--editor-border);
}